	module.exports = {"data":{"markdownRemark":{"html":"<p>やっと落ち着いてきたのでいろいろと。</p>\n<h2>転職しました</h2>\n<p>今まで埼玉の川越でプログラマやってましたが、9月一杯で諸々あって会社辞めました。で、それから1ヶ月何やってたのんかというと、アキバでまた懲りずにプログラマやったりしてます。しかもこの1ヶ月でケータイのゲームアプリ数本企画から製作全般担当したりとか、その後は携帯ビデオゲームのソフト開発に携わったりとか、猛烈にバタバタしています。お陰でPHPやFlash、Objective-Cにだいぶ慣れることが出来ました。Flashもスクリプトいじくるような本格的なモノは、同人レベルじゃあと1年は手を出すつもりはなかったんですが 'Ａ｀ Objective-Cに至っては未だにソース見ただけで拒絶反応覚えますし……(なぜ後発のC++言語の方が成功したのかを全身で感じ取ることが出来ましたよorz)</p>\n<h2>冬コミC75</h2>\n<p>当選したっぽい。仕事の都合であんまり家帰って無いので封筒確認出来て無いんですが、西「あ」列だったような。もっぺん確認のうえ再度告知します。</p>","frontmatter":{"cover":null,"date":"2008-11-05T00:00:00+09:00","strDate":"2008/11/4","title":"近況報告"}}},"pathContext":{"path":"/ja/blog/2008/11/05-近況報告/","langKey":"ja"}}

